@import '../../../index.scss';
.dd-wrapper {
  position: relative;
  min-width: 50px;

  .dd-header {
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    border: 1px solid #d3d3d3;
    height: 38px;

    align-items: center;
    cursor: pointer;
    user-select: none;
    span {
      display: flex;
      align-items: center;
      position: absolute;
      right: 5px;
    }
    &-title {
      font-weight: 300;
      margin: 2px 20px;
      margin-right: 30px;
      color: #666666;
    }
  }
  .dd-header-title-bold {
    font-weight: 600;
    padding-left: 10px;
  }

  .disable-bg {
    cursor: default;
    background-color: rgb(235, 235, 228);
    opacity: 0.4;
  }

  .dd-list {
    z-index: 10;
    position: absolute;
    width: 100%;
    border: 1px solid $SecondaryBorderColor;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background: #fff;
  }
  .dd-list-item {
    height: 42px;
    line-height: 42px;
    list-style-type: none;
    position: relative;
    text-align: left;
    padding-left: 18px;
    border-bottom: solid 1px rgba(211, 211, 211, 0.42);
    cursor: pointer;

    &:hover {
      background: #f8f8f8;
    }
    &:last-child {
      border-bottom: none;
    }

    span {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 15px;
    }
  }
}
