@import '../../../index.scss';

.hover-container {
  position: relative;
  width: 100%;
  &:hover {
    background-color: #ebedfd;
    > .hover-content-container {
      display: flex;
    }
  }
  .hover-content-container {
    display: none;
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    justify-content: flex-end;
    .content-edit {
      margin-right: 15px;
      font-size: 18px;
      width: 25px;
      height: 25px;
      background-color: $PrimaryBgColor;
      padding: 3px;
      border-radius: 50%;
      color: #fff;
      cursor: pointer;
    }
    .content-delete {
      margin-right: 15px;
      font-size: 18px;
      width: 25px;
      height: 25px;
      background-color: $PrimaryBgColor;
      padding: 3px;
      border-radius: 50%;
      color: #fff;
      cursor: pointer;
    }
    .content-clone {
      margin-right: 15px;
      font-size: 18px;
      width: 40px;
      height: 40px;
      line-height: 40px;
      background-color: $PrimaryBgColor;
      padding: 3px;
      border-radius: 50%;
      color: #fff;
      cursor: pointer;
    }
    .content-upload {
      margin-right: 15px;
      font-size: 18px;
      width: 25px;
      height: 25px;
      background-color: $PrimaryBgColor;
      padding: 3px;
      border-radius: 50%;
      color: #fff;
      cursor: pointer;
    }
  }
}
