@import '../../../index.scss';

.Popup-bg-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.71);
}

.Popup-container {
  width: 360px;
  // height: 161px;
  height:fit-content;
  border-radius: 10px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  border: solid 1px #d3d3d3;
  background-color: #ffffff;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  .Popup-title {
    width: 100%;
    height: 45px;
    margin-top: 16px;
    display: inline-block;
    font-weight: 500;
    font-family: Roboto;
    font-size: 14px;
    line-height: 1.14;
    text-align: center;
    color: #000000;
  }

  .Popup-message {
    width: 100%;
    // height: 56px;
    height:auto;
    font-family: Roboto;
    font-size: 14px;
    line-height: 1.43;
    text-align: center;
    color: #000000;
  }
  .Popup-control {
    // width: 360px;
    width: 100%;
    height: 42px;
    // width: 358px;
    // height: 38px;
    border: solid 1px #e2e2e2;
    background-color: #f0f0f0;
    // border-top-left-radius: 10px;
    // border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  .buttonflex {
    display: flex;
    margin-top: 10px;
    margin-bottom: 10px;
    flex-flow: row-reverse;
    justify-content: center;
    .primarysecondary {
      border-radius: 20px;
      text-align: center;
      font-family: Roboto;
      font-size: 12px;
      display: flex;
      justify-content: center;
      display: flex;
      margin-left: 9px;
      // border: solid 1px $PrimaryBorderColor;
      background: transparent;
      &:hover {
        //   background: $PrimaryBgLinear;
        color: #fff;
      }
    }
  }
  .primary {
    width: 83px;
    height: 22px;
    border-radius: 20px;
    border-radius: 20px;
    border: solid 1px $PrimaryBorderColor;
    background: $PrimaryBgColor;
    color: #fff;
    line-height: 100%;
  }

  .secondary {
    width: 83px;
    height: 22px;
    border-radius: 20px;
    border: solid 1px #4b50c3;
    background-color: #e9e9f3;

    .secchar {
      color: #4b50c3;
      width: 50px;
      height: 14px;
      font-family: Roboto;
      font-size: 12px;
    }
  }
}

